@import '~bulma';
@import url('https://fonts.googleapis.com/earlyaccess/notosanstc.css');
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');
@import 'helpers';
@import 'extends';
@import 'animation';

@import 'common/loading';
@import 'common/logo';
@import 'common/developer';
@import 'common/social';
@import 'common/menu-icon';
@import 'common/menu';
@import 'common/scroll-icon';
@import 'common/footer';
@import 'common/instafeeds';

@import 'page/error';
@import 'page/landing';
@import 'page/about';
@import 'page/contact';
@import 'page/article';
@import 'page/blog';


* {
  -webkit-tap-highlight-color: transparent !important;
}

html,
body {
  height: 100%;
  min-height: 100%;
}

body {
  color: $color-black;
  font-family: 'Noto Sans TC', 'Microsoft Yahei', '', sans-serif;
  letter-spacing: 2px;

  &.zh {
    letter-spacing: 2px;
  }
}

a {
  color: $color-black;
  position: relative;
  -webkit-transition: color .4s;
  -o-transition: color .4s;
  transition: color .4s;
  &:hover {
    color: $color-pink;
  }
}

.is-paragraph {
  font-size: 16px;
  line-height: 30px;
  @include mobile {
    font-size: 4vw;
    line-height: 2;
  }
}

.button {
  background-color: transparent;
  border-color: $color-grey-80;
  border-radius: 0;
  color: $color-grey-60;
  position: relative;
  -webkit-transition: color .3s;
  -o-transition: color .3s;
  transition: color .3s;

  &.is-medium {
    line-height: calc(45px - 13px);
  }
  &.is-large {
    line-height: calc(54px - 16px);
  }

  &.is-active {
    border-color: $color-pink;
  }

  &::before {
    @extend %before-after;
    background-color: $color-pink;
    -webkit-transition: width .2s;
    -o-transition: width .2s;
    transition: width .2s;
    width: 0;
    z-index: -1;
  }

  &:active,
  &:focus {
    border-color: $color-grey-80;
  }

  &:hover {
    @include desktop {
      border-color: $color-grey-80;
      color: $color-white;

      &::before {
        width: 100%;
      }
    }
  }
}

ul {
  margin-left: 0;
  li {
    list-style: none;
  }
}

p {
  &.is-underline {

    position: relative;

    &::before {
      @extend %before-after;
      background-color: $color-black;
      bottom: 0;
      height: 5px;
      top: inherit;
    }
  }
}

.title {
  font-family: 'Josefin Sans', 'Noto Sans TC', 'Microsoft Yahei', '', sans-serif;
  line-height: 1.5em;
}

.is-uppercase {
  font-family: 'Josefin Sans', 'Noto Sans TC', 'Microsoft Yahei', '', sans-serif;
  letter-spacing: 2.2px;
}

.is-pink {
  color: $color-pink;
}
.hero {
  &.is-pink {
    .hero-head,
    .hero-body,
    .hero-foot {
      background-color: $color-pink;
    }
    * {
      color: $color-white;
    }
  }
}



