.developer {
  height: 242px;
  left: 50px;
  margin-top: -20px;
  opacity: 0;
  position: fixed;
  top: calc(50% - 211px / 2);
  transform: rotate(-90deg) scale(1);
  width: auto;
  z-index: 100;

  &.show {
    @include animationfrombottom;
    animation: AnimationFromBottom .8s ease 1;
    animation-delay: 1.2s;
    animation-fill-mode: forwards;
  }
}



