.logo {
  @extend %hover-pink-line;
  @include mobile {
    left: 22px;
    top: 22px;
    margin-top: 5px;
  }
  @include desktop {
    transition: color .4s;

    &:hover {
      color: $color-pink;
    }
  }
  @include animationfromright;
  animation: AnimationFromLeft .6s ease 1;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
  color: $color-black;
  left: 50px;
  margin-left: -20px;
  opacity: 0;
  position: fixed;
  top: 35px;
  transition: color .2s;
  z-index: 2000;

  &.show {
    color: $color-white;
  }
}



